/**
* @description : This component renders a material app bar
*
* @prop className (string)            : Root element's class name
* @prop appbarStyle (object)          : Css style object for appbar
* @prop rightSection (elements)       : Html elements for right hand side of appbar
* @prop leftSection (array)           : An array of icons which will be placed on left hand side
* @prop title (string)                : Appbar title
* @prop titleStyle (object)           : Appbar title style
* @prop body (element)                : Html elements for appbar body which will be placed on the middle of it
* @prop bodyContainerStyle (object)   : Css style for body root element
* @prop isBodyAlwaysNested (string)   : If true, appbar's body will be placed on it's row
*
* @method render
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'react-flexbox-grid-aphrodite';

import BaseComponent from 'components/base/BaseComponent';
import './Style.css';

export class Appbar extends BaseComponent {
  render() {
    return (
      <div
        id="#appbar"
        className={'appbar ' + this.props.className}
        style={{ ...this.props.appbarStyle }}
      >
        <Row style={{ margin: 0 }}>
          <Col lg={3} md={3} sm={3} xs={6}>
            {this.props.rightSection}
          </Col>

          {this.props.isBodyAlwaysNested
            ? <Col
                xs={12}
                className={this.props.bodyContainerClassName}
                style={{ ...this.props.bodyContainerStyle }}
              >
                {this.props.body}
              </Col>
            : <Col
                lg={6}
                md={6}
                sm={6}
                xs={12}
                className={this.props.bodyContainerClassName}
                style={{ ...this.props.bodyContainerStyle }}
              >
                {this.props.body}
              </Col>}

          <Col lg={3} md={3} sm={3} xs={6}>
            <div
              style={{
                position: 'absolute',
                top: 8,
                left: 8,
                direction: 'ltr'
              }}
            >
              {this.props.leftSection.map(button => {
                return (
                  <div style={{ display: 'inline-block' }} key={Math.random()}>
                    {button}
                  </div>
                );
              })}
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}

Appbar.props = {
  className: PropTypes.string,
  appbarStyle: PropTypes.object,
  rightSection: PropTypes.element,
  title: PropTypes.string,
  titleStyle: PropTypes.object,
  body: PropTypes.element,
  bodyContainerStyle: PropTypes.object,
  isBodyAlwaysNested: PropTypes.bool,
  leftSection: PropTypes.array
};

Appbar.defaultProps = {
  className: '',
  appbarStyle: {},
  title: '',
  titleStyle: { marginRight: 8 },
  onMenuIconTouchTap: () =>
    process.env.NODE_ENV === 'development'
      ? console.log('Menu Icon tapped')
      : void 0,
  body: null,
  bodyContainerStyle: {},
  isBodyAlwaysNested: false,
  leftSection: []
};

export default connect(state => {
  return {
    windowWidth: state.app.windowWidth
  };
})(Appbar);



