/**
* @description This class is responsible for rendering and handling
* all of the required components for showing user's messsages and some other interactions
*
* @method constructor
* @method componentDidMount
* @method componentWillUpdate
* @method componentWillUnmount
* @method render
* @method load
* @method updateScrollPosition
* @method getRandomElement
* @method generateMessagesCard
* @method handleLoadMore
* @method handleLoadMoreMessage
* @method handleLoadMoreRetryTouchTap
* @method handleRefreshTouchTap
* @method handleOnBackTouchTap
* @method handleEditTouchTap
* @method handleCloseTouchTap
* @method handleSelectAllTouchTap
* @method handleSelectNoneTouchTap
* @method handleDeleteTouchTap
* @method handleMessageTouchTap
* @method changeMessageSeenStatus
* @method updatedSelectedMessages
* @method handleSnackClose
* @method handleDialogClose
* @method handleDeleteDialogConfirmTouchTap
* @method handleMessageDeleteTouchTap
* @method handleScrollToTopFabTouchTap
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/
import React from 'react';
import { connect } from 'react-redux';
import { hashHistory } from 'react-router';

import BaseComponent from 'components/base/BaseComponent';
import DashboardUI from './DashboardUI';
import { fetchMessages, deleteMessage, setMessageAsSeen } from 'api/Board';
import {
  Global as GlobalStrings,
  Dashboard as DashboardStrings
} from 'i18n/Persian';
import { smoothScroll } from 'helpers/Helpers';
import { updateUser } from 'redux/actions/UserActions';

export class Dashboard extends BaseComponent {
  constructor(props) {
    super(props);

    // Holds default states of the component
    this.DEFAULT_STATES = {
      mainViewClassName: 'bottom-to-top-move-in', // To hold main view's class name,
      appbarClassName: 'top-to-bottom-move-in', // To hold appbar's class name
      isLoading: true, // If true then component will show a loading
      messages: undefined, // To hold all of the user's messages
      hasMoreMessage: true, // To track if user has more message to fetch
      messageFetchOffset: 0, // After each request this will be increased if the number of returned messages are equal to the messageFetchLimit */,
      editingMode: false, // To track if editing mode is enabled or not
      wasLoadMoreSuccessful: undefined, // Indicates if a load more was successful or not
      selectedMessagesIds: [], // To track the selected messages ids to delete. Numbers of ids will be shown on appbar
      isSnackOpen: false, // To specify whether snack should be open or not
      snackMessage: '', // To set snack messages
      isDeleteDialogOpen: false, // To specify whether delete dialog should be open or not
      isDoingTask: false, // This is used for dialogs and if its true, dialogs will show a circular progress
      repack: false, // If true, Masonry component will reorder the bricks(each item on Masonry)
      openedMessage: {}, // To holds clicked message data to show it in a dialog
      showScrollToTopFab: false, // If true a fab button will appear to help user to scroll to top of the page
      windowYPosition: 0 // This is for returning user to exact location he was after he returned from MessageUI
    };

    // Holds default data of the component (changing these data wont trigger a rerender)
    this.DEFAULT_DATA = {
      isLoadingMoreMessage: false // To track whether component is loading more message or not
    };

    // Some predefined heights for each message card
    this.HEIGHTS = [
      { card: 200, messageBody: 100 },
      { card: 300, messageBody: 200 },
      { card: 300, messageBody: 200 },
      { card: 400, messageBody: 300 },
      { card: 400, messageBody: 300 },
      { card: 450, messageBody: 350 }
    ];

    // To hold all of the callback handlers
    this.handlers = {
      onAppbarRightSectionTouchTap: this.handleAppbarRightSectionTouchTap,
      onRefreshTouchTap: this.handleRefreshTouchTap,
      onEditTouchTap: this.handleEditTouchTap,
      onCloseTouchTap: this.handleCloseTouchTap,
      onSelectAllTouchTap: this.handleSelectAllTouchTap,
      onSelectNoneTouchTap: this.handleSelectNoneTouchTap,
      onDeleteTouchTap: this.handleDeleteTouchTap,
      onLoadMore: this.handleLoadMore,
      onLoadMoreRetryTouchTap: this.handleLoadMoreRetryTouchTap,
      onMessageTouchTap: this.handleMessageTouchTap,
      onSnackClose: this.handleSnackClose,
      onDialogClose: this.handleDialogClose,
      onDeleteDialogConfirmTouchTap: this.handleDeleteDialogConfirmTouchTap,
      onMessageDeleteTouchTap: this.handleMessageDeleteTouchTap,
      onBackTouchTap: this.handleOnBackTouchTap,
      onScrollToTopFabTouchTap: this.handleScrollToTopFabTouchTap
    };

    this.state = this.DEFAULT_STATES;
    this.data = this.DEFAULT_DATA;

    this.updateScrollPosition = this.updateScrollPosition.bind(this);
  }

  componentDidMount() {
    this.load();
    window.addEventListener('scroll', this.updateScrollPosition);
  }

  componentWillUpdate(nextProps, nextState) {
    process.env.NODE_ENV === 'development'
      ? console.log('New Component states =>', nextState)
      : void 0;
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.updateScrollPosition);
  }

  render() {
    return <DashboardUI states={this.state} handlers={this.handlers} />;
  }

  /**
  * @description : Resets the component and fetch data from the server
  *
  * @author Armin Eslami
  */
  load() {
    // Reset components state
    this.setState(this.DEFAULT_STATES);
    // Fetch data from the server
    fetchMessages(
      this.props.user.token,
      this.props.user.settings.messageFetchLimit,
      this.state.messageFetchOffset,
      this.props.user.settings.combinedFetch
    ).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('fetchMessages() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data && res.data.messages) {
          this.props.dispatch(
            updateUser({ token: res.data.token ? res.data.token : null })
          );

          // If server has returend hasMore use it, otherwise set it to true so maybe on next fetch its real value will be returned
          var hasMoreMessage = res.data.hasOwnProperty('hasMore')
            ? res.data.hasMore
            : true;

          // Prepare messages for showing on the component by adding some extra keys like height to them
          var messages = this.generateMessagesCard(res.data.messages);

          // Set new offset
          var messageFetchOffset = hasMoreMessage
            ? this.state.messageFetchOffset + this.props.user.settings.messageFetchLimit
            : this.state.messageFetchOffset;

          // Update states
          this.setState({
            messages,
            messageFetchOffset: messageFetchOffset,
            hasMoreMessage,
            isLoading: false
          });
        } else {
          this.setState({ isLoading: false });
        }
      },
      err => {
        process.env.NODE_ENV === 'development'
          ? console.log('fetchMessages() ended with error', err)
          : void 0;
        this.setState({ isLoading: false });
      }
    );
  }

  /**
  * @description : A listener for scroll position change
  *
  * @author Armin Eslami
  */
  updateScrollPosition() {
    var winHeight = window.innerHeight;

    // Annoying to compute doc height due to browser inconsistency
    var body = document.body;
    var html = document.documentElement;
    var docHeight = Math.max(
      body.scrollHeight,
      body.offsetHeight,
      html.clientHeight,
      html.scrollHeight,
      html.offsetHeight
    );

    // To get scroll position with a browsers-wide solution
    var value =
      window.pageYOffset ||
      document.documentElement.scrollTop ||
      document.body.scrollTop ||
      0; //document.body.scrollTop;

    var max = docHeight - winHeight;
    var percent = value / max * 100;

    if (percent >= 20 && this.state.showScrollToTopFab === false) {
      this.setState({ showScrollToTopFab: true });
    } else if (percent < 20 && this.state.showScrollToTopFab === true) {
      this.setState({ showScrollToTopFab: false });
    }
  }

  /**
  * @description : Picks a randome data from given array resource
  *
  * @author Armin Eslami
  *
  * @param array (array) : An array of data for picking one of its data randomly
  *
  * @return randomly picked data
  */
  getRandomElement = array => array[Math.floor(Math.random() * array.length)];

  /**
  * @description : Adds some extra data to each message object.
  *
  * @author Armin Eslami
  *
  * @param messages (array) : An array of messages object
  *
  * @return an array with messages object with some more data added to each of them
  */
  generateMessagesCard = messages => {
    var finalMessages = messages.map(message => {
      var heights = this.getRandomElement(this.HEIGHTS);
      return {
        ...message,
        isSelected: false,
        cardHeight: `${heights.card}px`,
        bodyHeight: `${heights.messageBody}px`
      };
    });
    return finalMessages;
  };

  /**
  * @description : Tries to fetch more messsages when user scroll to bottom of page
  *
  * @author Armin Eslami
  */
  handleLoadMore = () => {
    if (this.data.isLoadingMoreMessage !== true) {
      this.data.isLoadingMoreMessage = true;
      this.handleLoadMoreMessage();
    }
  };

  /**
  * @description : Calls proper api function to fetch more messages
  *
  * @author Armin Eslami
  */
  handleLoadMoreMessage = () => {
    // Fetch data from the server
    fetchMessages(
      this.props.user.token,
      this.props.user.settings.messageFetchLimit,
      this.state.messageFetchOffset,
      this.props.user.settings.combinedFetch
    ).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('fetchMessages() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data && res.data.messages) {
          this.props.dispatch(
            updateUser({ token: res.data.token ? res.data.token : null })
          );

          // If server has returend hasMore use it, otherwise set it to true so maybe on next fetch its real value will be returned
          var hasMoreMessage = res.data.hasOwnProperty('hasMore')
            ? res.data.hasMore
            : true;

          // Prepare messages for showing on the component by adding some extra keys like height to them
          var messages = this.generateMessagesCard(res.data.messages);
          // Add new messages to old ones
          messages = [...this.state.messages, ...messages];

          // Set new offset
          var messageFetchOffset = hasMoreMessage
            ? this.state.messageFetchOffset + this.props.user.settings.messageFetchLimit
            : this.state.messageFetchOffset;

          // Update states
          this.setState({
            messages,
            messageFetchOffset: messageFetchOffset,
            hasMoreMessage,
            wasLoadMoreSuccessful: true
          });
          this.data.isLoadingMoreMessage = false;
        } else {
          this.setState({
            hasMoreMessage: false,
            wasLoadMoreSuccessful: false
          });
          this.data.isLoadingMoreMessage = false;
        }
      },
      err => {
        process.env.NODE_ENV === 'development'
          ? console.log('fetchMessages() ended with error', err)
          : void 0;
        this.setState({ hasMoreMessage: false, wasLoadMoreSuccessful: false });
        this.data.isLoadingMoreMessage = false;
      }
    );
  };

  /**
  * @description : Handler for tapping on retry button of load more (it appears when load more fails)
  *
  * @author Armin Eslami
  */
  handleLoadMoreRetryTouchTap = () => {
    this.setState({ hasMoreMessage: true, wasLoadMoreSuccessful: undefined });
  };

  /**
  * @description : Handler for tapping on appbar's right section
  *
  * @author Armin Eslami
  */
  handleAppbarRightSectionTouchTap = () => {
    this.setState({
      mainViewClassName: 'top-to-bottom-move-out',
      appbarClassName: 'bottom-to-top-move-out'
    });
    setTimeout(
      () => hashHistory.replace('/profile'), // TODO: change hashHistory to browserHistory on production !
      1000
    );
  };

  /**
  * @description : Handler for tapping on appbar's refresh button
  *
  * @author Armin Eslami
  */
  handleRefreshTouchTap = () => {
    if (!this.state.isLoading) {
      this.load();
    }
  };

  /**
  * @description : Handler for tapping on appbar's back button on @class MessageUI
  *
  * @author Armin Eslami
  */
  handleOnBackTouchTap = () => {
    this.setState({ mainViewClassName: 'left-to-right-move-out' });
    setTimeout(() => {
      this.setState({
        openedMessage: {},
        mainViewClassName: 'left-to-right-move-in'
      });
      window.scroll(0, this.state.windowYPosition);
    }, 1000);
  };

  /**
  * @description : Handler for tapping on appbar's edit button
  *
  * @author Armin Eslami
  */
  handleEditTouchTap = () => {
    if (!this.state.isLoading) {
      this.setState({ editingMode: true });
    }
  };

  /**
  * @description : Handler for tapping on appbar's close button
  *
  * @author Armin Eslami
  */
  handleCloseTouchTap = () => {
    var messages = this.state.messages;
    var counter = 0; // To track how many selected message is finded

    if (
      this.state.selectedMessagesIds &&
      this.state.selectedMessagesIds.length > 0
    ) {
      for (var i = 0; i < messages.length; i++) {
        if (messages[i].isSelected) {
          messages[i].isSelected = false;
          counter++;
        }

        /**
           * If these two are equal, it means all of the selected messages are already finded so break 
           * and dont waste the time
           */
        if (counter === this.state.selectedMessagesIds.length) {
          break;
        }
      }
    }

    this.setState({ editingMode: false, messages, selectedMessagesIds: [] });
  };

  /**
  * @description : Handler for tapping on appbar's select all button
  *
  * @author Armin Eslami
  */
  handleSelectAllTouchTap = () => {
    var selectedMessagesIds = [];

    selectedMessagesIds = this.state.messages.map(message => {
      message.isSelected = true;
      return message.id;
    });

    this.setState({ selectedMessagesIds });
  };

  /**
  * @description : Handler for tapping on appbar's select none button
  *
  * @author Armin Eslami
  */
  handleSelectNoneTouchTap = () => {
    var messages = this.state.messages;
    messages.map(message => {
      message.isSelected = false;
      return null;
    });

    this.setState({ messages, selectedMessagesIds: [] });
  };

  /**
  * @description : Handler for tapping on appbar's delete button
  *
  * @author Armin Eslami
  */
  handleDeleteTouchTap = () => {
    if (this.state.selectedMessagesIds.length === 0) {
      this.setState({
        isSnackOpen: true,
        snackMessage: DashboardStrings.noMessageIsSelected
      });
    } else {
      this.setState({ isDeleteDialogOpen: true });
    }
  };

  /**
  * @description : Handler for tapping on message cards
  *
  * @author Armin Eslami
  *
  * @param messageId (string) : Touched message's id
  */
  handleMessageTouchTap = messageId => {
    var messages = this.state.messages;
    var i = 0;

    if (this.state.editingMode) {
      /**
       * Since editing mode is enabled, then toggle isSelected status of selected message.
       * Also update @prop selectedMessagesIds by calling @method updatedSelectedMessages
       */
      for (i = 0; i < messages.length; i++) {
        if (messages[i].id === messageId) {
          this.updatedSelectedMessages(messageId, !messages[i].isSelected);
          messages[i].isSelected = !messages[i].isSelected;
          break;
        }
      }
      this.setState({ messages });
    } else {
      for (i = 0; i < messages.length; i++) {
        if (messages[i].id === messageId) {
          this.setState({ mainViewClassName: 'right-to-left-move-out' });
          break;
        }
      }

      setTimeout(
        () =>
          this.setState({
            openedMessage: messages[i],
            mainViewClassName: 'right-to-left-move-in',
            showScrollToTopFab: false,
            windowYPosition:
              window.pageYOffset ||
              document.documentElement.scrollTop ||
              document.body.scrollTop ||
              0
          }),
        1000
      );

      // If the message's seen status is false, then change it to true
      // because its now opend.
      if (messages[i].seen === false) {
        this.changeMessageSeenStatus(messages[i].id);
      }
    }
  };

  /**
  * @description : Sends a request to set message seen status to true
  *
  * @author Armin Eslami
  *
  * @param messageId (string) : Id of a message which it's seen status should be changed
  */
  changeMessageSeenStatus = messageId => {
    setMessageAsSeen(this.props.user.token, messageId).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('changeMessageSeenStatus() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({ token: res.data.token ? res.data.token : null })
          );

          // change seen flag on messages object
          var messages = this.state.messages;
          for (var i = 0; i < messages.length; i++) {
            if (messages[i].id === messageId) {
              messages[i].seen = true;
              break;
            }
          }

          this.setState({ messages });
        }
      },
      err => {
        process.env.NODE_ENV === 'development'
          ? console.log('changeMessageSeenStatus() ended with error', err)
          : void 0;
      }
    );
  };

  /**
  * @description : Updates @prop selectedMessagesIds so component be able to track selected messages.
  *
  * @author Armin Eslami
  *
  * @param messageId (string) : Id of a message which is touched
  * @param messageId (isSelected) : Selcetion status which is either true or false
  */
  updatedSelectedMessages = (messageId, isSelected) => {
    if (isSelected) {
      this.setState({
        selectedMessagesIds: [...this.state.selectedMessagesIds, messageId]
      });
    } else {
      var selectedMessagesIds = this.state.selectedMessagesIds;

      for (var i = 0; i < selectedMessagesIds.length; i++) {
        if (selectedMessagesIds[i] === messageId) {
          selectedMessagesIds.splice(i, 1);
        }
      }

      this.setState({ selectedMessagesIds });
    }
  };

  /**
  * @description : Handler for closing snack
  *
  * @author Armin Eslami
  */
  handleSnackClose = () => {
    this.setState({ isSnackOpen: false, snackMessage: '' });
  };

  /**
  * @description : Handler for closing dialog
  *
  * @author Armin Eslami
  */
  handleDialogClose = () => {
    this.setState({ isDeleteDialogOpen: false, openedMessage: {} });
  };

  /**
  * @description : Handler for tapping on confirm button of delete dialog
  *
  * @author Armin Eslami
  */
  handleDeleteDialogConfirmTouchTap = () => {
    this.setState({ isDoingTask: true });

    deleteMessage(this.props.user.token, this.state.selectedMessagesIds).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('deleteMessage() ended with result', res)
          : void 0;

        if (res.code === 200) {
          this.props.dispatch(
            updateUser({ token: res.data.token ? res.data.token : null })
          );

          var messages = this.state.messages.filter(message => {
            return this.state.selectedMessagesIds.indexOf(message.id) === -1;
          });

          this.setState({
            messages,
            isDoingTask: false,
            isDeleteDialogOpen: false,
            repack: true,
            editingMode: messages.length > 0,
            selectedMessagesIds: []
          });
        } else {
          this.setState({
            isDoingTask: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isDoingTask: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };

  /**
  * @description : Handler for tapping on delete button of each message on @class MessageUI
  *
  * @author Armin Eslami
  *
  * @param messageId (string) : Id of a message which is going to be deleted
  */
  handleMessageDeleteTouchTap = messageId => {
    this.setState({ isDoingTask: true });

    if (this.state.isDoingTask) {
      return;
    }

    deleteMessage(this.props.user.token, [messageId]).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('deleteMessage() ended with result', res)
          : void 0;

        if (res.code === 200) {
          this.props.dispatch(
            updateUser({ token: res.data.token ? res.data.token : null })
          );

          if (this.state.messages === undefined) {
            return;
          }

          var messages = this.state.messages.filter(message => {
            return message.id !== messageId;
          });

          this.setState({
            messages,
            isDoingTask: false,
            openedMessage: {},
            repack: true,
            selectedMessagesIds: []
          });
        } else {
          this.setState({
            isDoingTask: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isDoingTask: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };

  /**
  * @description : Handler for tapping on back to top fab button which will cause
  * to scroll back to top by using appbar id
  *
  * @author Armin Eslami
  */
  handleScrollToTopFabTouchTap = () => {
    smoothScroll.scrollTo('#appbar');
  };
} // End of class

export default connect(state => {
  return {
    user: state.user
  };
})(Dashboard);



