/**
* @description This component renders the main ui of @class Dashboard
*
* @method render
* @method renderAppbar
* @method renderUI
* @method renderAppbarActionButtons
* @method renderHelperComponents
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import { connect } from 'react-redux';
import { Card } from 'material-ui/Card';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import Masonry from 'react-masonry-infinite';
import CircularProgress from 'material-ui/CircularProgress';
import IconButton from 'material-ui/IconButton';
import SvgIcon from 'material-ui/SvgIcon';
import Snackbar from 'material-ui/Snackbar';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import Avatar from 'material-ui/Avatar';
import LinesEllipsis from 'react-lines-ellipsis';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ListItem from 'material-ui/List/ListItem';
import TouchRipple from 'material-ui/internal/TouchRipple';
// import HTMLEllipsis from 'react-lines-ellipsis/lib/html';

import BaseComponent from 'components/base/BaseComponent';
import MessageUI from './MessageUI';
import Appbar from 'components/appbar/Appbar';
import Loading from 'components/loading/Loading';
import './style.css';
import {
  Global as GlobalStrings,
  Dashboard as DashboardStrings
} from 'i18n/Persian';
import { OPERATION_FAILED, NO_MESSAGE_RECIVED } from 'components/StatesIcons';
import {
  getFormattedDateFromTimestamp,
  convertEnDigits
} from 'helpers/Helpers';

const Refresh = props =>
  <SvgIcon {...props}>
    <path d="M17.65,6.35C16.2,4.9 14.21,4 12,4A8,8 0 0,0 4,12A8,8 0 0,0 12,20C15.73,20 18.84,17.45 19.73,14H17.65C16.83,16.33 14.61,18 12,18A6,6 0 0,1 6,12A6,6 0 0,1 12,6C13.66,6 15.14,6.69 16.22,7.78L13,11H20V4L17.65,6.35Z" />
  </SvgIcon>;

const Edit = props =>
  <SvgIcon {...props}>
    <path d="M20.71,7.04C21.1,6.65 21.1,6 20.71,5.63L18.37,3.29C18,2.9 17.35,2.9 16.96,3.29L15.12,5.12L18.87,8.87M3,17.25V21H6.75L17.81,9.93L14.06,6.18L3,17.25Z" />
  </SvgIcon>;

const Delete = props =>
  <SvgIcon {...props}>
    <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z" />
  </SvgIcon>;

const SelectAll = props =>
  <SvgIcon {...props}>
    <path d="M9,9H15V15H9M7,17H17V7H7M15,5H17V3H15M15,21H17V19H15M19,17H21V15H19M19,9H21V7H19M19,21A2,2 0 0,0 21,19H19M19,13H21V11H19M11,21H13V19H11M9,3H7V5H9M3,17H5V15H3M5,21V19H3A2,2 0 0,0 5,21M19,3V5H21A2,2 0 0,0 19,3M13,3H11V5H13M3,9H5V7H3M7,21H9V19H7M3,13H5V11H3M3,5H5V3A2,2 0 0,0 3,5Z" />
  </SvgIcon>;

const SelectNone = props =>
  <SvgIcon {...props}>
    <path d="M1,4.27L2.28,3L21,21.72L19.73,23L17,20.27V21H15V19H15.73L5,8.27V9H3V7H3.73L1,4.27M20,3A1,1 0 0,1 21,4V5H19V3H20M15,5V3H17V5H15M11,5V3H13V5H11M7,5V3H9V5H7M11,21V19H13V21H11M7,21V19H9V21H7M4,21A1,1 0 0,1 3,20V19H5V21H4M3,15H5V17H3V15M21,15V17H19V15H21M3,11H5V13H3V11M21,11V13H19V11H21M21,7V9H19V7H21Z" />
  </SvgIcon>;

const Close = props =>
  <SvgIcon {...props}>
    <path d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z" />
  </SvgIcon>;

const Check = props =>
  <SvgIcon {...props}>
    <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
  </SvgIcon>;

const Back = props =>
  <SvgIcon {...props}>
    <path d="M21,11H6.83L10.41,7.41L9,6L3,12L9,18L10.41,16.58L6.83,13H21V11Z" />
  </SvgIcon>;

const TopArrow = props =>
  <SvgIcon {...props}>
    <path d="M7.41,15.41L12,10.83L16.59,15.41L18,14L12,8L6,14L7.41,15.41Z" />
  </SvgIcon>;

class DashboardUI extends BaseComponent {
  render() {
    return (
      <div>
        {this.renderAppbar()}
        <div className="component" style={{ overflowX: 'hidden' }}>
          {this.renderUI()}
        </div>
        {this.renderHelperComponents()}
      </div>
    );
  }

  renderAppbar = () => {
    return (
      <Appbar
        className={this.props.states.appbarClassName}
        rightSection={
          <div
            style={{
              cursor: 'pointer',
              position: 'relative',
              top:4,
            }}
            onTouchTap={this.props.handlers.onAppbarRightSectionTouchTap}
          >

          {
            this.props.user && this.props.user.avatar ?
            <ListItem
              disabled={true}
              innerDivStyle={{paddingLeft:64}}
              leftAvatar={
                <Avatar
                  src={
                    process.env.REACT_APP_DOMAIN +
                    process.env.REACT_APP_IMAGES_DIRECTORY +
                    this.props.user.avatar
                  }
                  size={40}
                  style={{ left: 0 }}
                />
              }
            >
              <span className="truncate" style={{marginTop:2}}>{ `${this.props.user.firstName}  ${this.props.user.lastName}` }</span>
            </ListItem>
            :
            <ListItem
              disabled={true}
              innerDivStyle={{paddingLeft:64}}
              leftAvatar={
                <Avatar size={40} style={{ left: 8 }}>
                  {this.props.user && this.props.user.firstName
                    ? this.props.user.firstName.substring(0, 1)
                    : 'ن'}
                </Avatar>
              }
            >
              <span className="truncate" style={{marginTop:2}}>{ `${this.props.user.firstName}  ${this.props.user.lastName}` }</span>
            </ListItem>
          }

          </div>
        }
        leftSection={this.renderAppbarActionButtons()}
      />
    );
  };

  renderUI = () => {
    var component = null;

    if (this.props.states.isLoading) {
      component = <Loading style={{ marginTop: '10%' }} />;
    } else if (!this.props.states.messages) {
      component = (
        <Row center="xs">
          <Col lg={4} md={4} sm={6} xs={11} style={{ marginTop: '5%' }}>
            {OPERATION_FAILED}
            <p
              className="noMessage"
              style={{ fontSize: '.8rem', color: '#999999', marginTop: 16 }}
            >
              {GlobalStrings.fatalError}
            </p>
          </Col>
        </Row>
      );
    } else if (this.props.states.messages.length === 0) {
      component = (
        <Row center="xs">
          <Col lg={4} md={4} sm={6} xs={11} style={{ marginTop: '5%' }}>
            {NO_MESSAGE_RECIVED}
            <p
              className="noMessage"
              style={{ fontSize: '.8rem', color: '#999999', marginTop: 16 }}
            >
              {DashboardStrings.youHaveNoMessage}
            </p>
          </Col>
        </Row>
      );
    } else if (Object.keys(this.props.states.openedMessage).length > 0) {
      component = (
        <MessageUI
          className={this.props.states.mainViewClassName}
          message={this.props.states.openedMessage}
          isDoingTask={this.props.states.isDoingTask}
          onDelete={this.props.handlers.onMessageDeleteTouchTap}
        />
      );
    } else {
      component = (
        <div className="masnoryContainer">
          <Masonry
            style={{ margin: '16px auto' }}
            className={this.props.states.mainViewClassName}
            hasMore={
              this.props.states.hasMoreMessage && !this.props.states.editingMode
            }
            pack={this.props.states.repack}
            loader={
              <div style={{ textAlign: 'center', marginTop: 32 }}>
                <CircularProgress size={30} />
              </div>
            }
            loadMore={
              this.props.states.hasMoreMessage && !this.props.states.editingMode
                ? this.props.handlers.onLoadMore
                : () => null
            }
          >
            {this.props.states.messages.map((message, i) => {
              return (
                <Card
                  key={message.id}
                  className="card"
                  onTouchTap={() =>
                    this.props.handlers.onMessageTouchTap(message.id)}
                  style={{
                    height: message.cardHeight,
                    opacity: message.isSelected ? '.5' : '1'
                  }}
                >
                  <TouchRipple>

                      {message.isSelected
                        ? <Check
                            style={{
                              height: 48,
                              width: 'auto',
                              position: 'absolute',
                              top: 0,
                              bottom: 0,
                              right: 0,
                              left: 0,
                              margin: 'auto'
                            }}
                          />
                        : void 0}

                      <Row>
                        <Col xs={3}>
                          {message.avatar
                            ? <div>
                                <Avatar
                                  size={48}
                                  className="avatar"
                                  alt=''
                                  src={
                                    process.env.REACT_APP_DOMAIN +
                                    process.env.REACT_APP_IMAGES_DIRECTORY +
                                    message.avatar
                                  }
                                />
                              </div>
                            : <Avatar size={48} className="avatar" alt=''>
                                {message.sender
                                  ? message.sender.substring(0, 1)
                                  : 'ن'}
                              </Avatar>}
                        </Col>

                        <Col xs={8}>
                          <div style={{ marginTop: 12 }}>
                            <LinesEllipsis
                              text={message.sender}
                              maxLine="1"
                              ellipsis="..."
                              trimRight
                              basedOn="letters"
                            />
                            <span style={{ display: 'block', fontSize: '.8rem' }}>
                              {getFormattedDateFromTimestamp(message.date)}
                            </span>
                          </div>
                        </Col>

                        {message.hasOwnProperty('seen') && message.seen === false
                          ? <Col xs={1}>
                              <span
                                style={{
                                  width: 8,
                                  height: 8,
                                  background: '#FF5722',
                                  position: 'absolute',
                                  top: 16,
                                  left: 16,
                                  borderRadius: '100%'
                                }}
                              />
                            </Col>
                          : void 0}
                      </Row>

                      <div
                        style={{
                          overflow: 'hidden',
                          padding: 16,
                          height: message.bodyHeight
                        }}
                      >
                        <LinesEllipsis
                          text={message.title}
                          maxLine="1"
                          ellipsis="..."
                          trimRight
                          basedOn="letters"
                        />
                        {/* dangerouslySetInnerHTML helps to render string html, as a replacement for innerHTML of jquery */}
                        <div
                          className="messageBody"
                          dangerouslySetInnerHTML={{ __html: message.body }}
                        />

                        {/*
                          <HTMLEllipsis
                              unsafeHTML={message.body}
                              maxLine='5'
                              ellipsis='...'
                              basedOn='letters'
                          />
                        */}
                      </div>

                    </TouchRipple>
                </Card>
              );
            })}
          </Masonry>
          {this.props.states.wasLoadMoreSuccessful === false
            ? <div style={{ textAlign: 'center' }}>
                <IconButton
                  style={{
                    background: '#ffffff',
                    borderRadius: '100%',
                    width: 'auto',
                    height: 48,
                    boxShadow:
                      'rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px'
                  }}
                  onTouchTap={this.props.handlers.onLoadMoreRetryTouchTap}
                >
                  <Refresh />
                </IconButton>
                <p
                  style={{
                    color: '#5e5e5e',
                    fontSize: '.8rem',
                    marginBottom: 0
                  }}
                >
                  {GlobalStrings.fetchError}
                </p>
              </div>
            : void 0}
        </div>
      );
    }

    return component;
  };

  renderAppbarActionButtons = () => {
    var actionButtons = [];

    if (Object.keys(this.props.states.openedMessage).length > 0) {
      actionButtons.push(
        <IconButton
          onTouchTap={() => setTimeout(this.props.handlers.onBackTouchTap, 200)}
        >
          <Back />
        </IconButton>,
        <IconButton
          onTouchTap={() =>
            setTimeout(this.props.handlers.onRefreshTouchTap, 200)}
        >
          <Refresh />
        </IconButton>
      );
    } else if (!this.props.states.editingMode) {
      actionButtons.push(
        <IconButton
          onTouchTap={() =>
            setTimeout(this.props.handlers.onRefreshTouchTap, 200)}
        >
          <Refresh />
        </IconButton>
      );
      if (this.props.states.messages && this.props.states.messages.length > 0) {
        actionButtons.push(
          <IconButton
            onTouchTap={() =>
              setTimeout(this.props.handlers.onEditTouchTap, 200)}
          >
            <Edit />
          </IconButton>
        );
      }
    } else {
      actionButtons.push(
        <IconButton
          className="hint--bottom"
          aria-label={GlobalStrings.close}
          onTouchTap={() =>
            setTimeout(this.props.handlers.onCloseTouchTap, 200)}
        >
          <Close />
        </IconButton>,
        <div>
          {this.props.states.selectedMessagesIds.length === 0 ||
          this.props.states.selectedMessagesIds.length <
            this.props.states.messages.length
            ? <IconButton
                className="hint--bottom"
                aria-label={DashboardStrings.selectAll}
                onTouchTap={this.props.handlers.onSelectAllTouchTap}
              >
                <SelectAll />
              </IconButton>
            : <IconButton
                className="hint--bottom"
                aria-label={DashboardStrings.selectNone}
                onTouchTap={this.props.handlers.onSelectNoneTouchTap}
              >
                <SelectNone />
              </IconButton>}
        </div>,
        <div>
          <IconButton
            className="hint--bottom"
            aria-label={DashboardStrings.delete}
            onTouchTap={this.props.handlers.onDeleteTouchTap}
          >
            <Delete />
          </IconButton>
          <span style={{ position: 'absolute', top: 16 }}>
            {this.props.states.selectedMessagesIds.length > 0
              ? `(${convertEnDigits(
                  this.props.states.selectedMessagesIds.length
                )})`
              : void 0}
          </span>
        </div>
      );
    }

    return actionButtons;
  };

  renderHelperComponents = () => {
    return (
      <div>
        {this.props.states.showScrollToTopFab
          ? <FloatingActionButton
              className="right-to-left-move-in"
              mini={true}
              onTouchTap={this.props.handlers.onScrollToTopFabTouchTap}
              style={{
                margin: 0,
                top: 'auto',
                bottom: 20,
                left: 16,
                position: 'fixed',
                zIndex: 999
              }}
            >
              <TopArrow />
            </FloatingActionButton>
          : void 0}

        <Snackbar
          open={this.props.states.isSnackOpen}
          message={this.props.states.snackMessage}
          autoHideDuration={2000}
          onRequestClose={this.props.handlers.onSnackClose}
          style={{ textAlign: 'center' }}
        />

        {/* Delete Dialog */}
        <Dialog
          title={DashboardStrings.messageDelete}
          style={{ direction: 'ltr' }}
          titleClassName="dialogTitle"
          bodyClassName="dialogBody"
          contentStyle={{
            width: this.props.app.windowWidth < 768 ? '90%' : '40%'
          }}
          actions={
            this.props.states.isDoingTask
              ? [<CircularProgress size={25} style={{ right: 16 }} />]
              : [
                  <FlatButton
                    label={GlobalStrings.cancel}
                    primary={true}
                    onClick={this.props.handlers.onDialogClose}
                  />,
                  <FlatButton
                    label={GlobalStrings.confirm}
                    primary={true}
                    // keyboardFocused={true}
                    onClick={this.props.handlers.onDeleteDialogConfirmTouchTap}
                  />
                ]
          }
          modal={this.props.states.isDoingTask}
          open={this.props.states.isDeleteDialogOpen}
          onRequestClose={this.props.handlers.onDialogClose}
        >
          {DashboardStrings.deleteDialogBody}
        </Dialog>
      </div>
    );
  };
} // End of class

export default connect(state => {
  return {
    app: state.app,
    user: state.user,
  };
})(DashboardUI);



