/**
* @description This component renders A ui for viewing a message.
*
* @method render
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import { Card } from 'material-ui/Card';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import CircularProgress from 'material-ui/CircularProgress';
import IconButton from 'material-ui/IconButton';
import SvgIcon from 'material-ui/SvgIcon';
import Avatar from 'material-ui/Avatar';

import BaseComponent from 'components/base/BaseComponent';
import { Dashboard as DashboardStrings } from 'i18n/Persian';
import { getFormattedDateFromTimestamp } from 'helpers/Helpers';

const Delete = props =>
  <SvgIcon {...props}>
    <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z" />
  </SvgIcon>;

const Attachment = props =>
  <SvgIcon {...props} style={{ width: '21px', height: '21px' }}>
    <path d="M16.5,6V17.5A4,4 0 0,1 12.5,21.5A4,4 0 0,1 8.5,17.5V5A2.5,2.5 0 0,1 11,2.5A2.5,2.5 0 0,1 13.5,5V15.5A1,1 0 0,1 12.5,16.5A1,1 0 0,1 11.5,15.5V6H10V15.5A2.5,2.5 0 0,0 12.5,18A2.5,2.5 0 0,0 15,15.5V5A4,4 0 0,0 11,1A4,4 0 0,0 7,5V17.5A5.5,5.5 0 0,0 12.5,23A5.5,5.5 0 0,0 18,17.5V6H16.5Z" />
  </SvgIcon>;

export class MessageUI extends BaseComponent {
  componentDidMount() {
    window.scroll(0, 0);
  }
  render() {
    return (
      <Row center="xs" className={this.props.className}>
        <Col lg={6} md={10} sm={10} xs={12}>
          <Card style={{ padding: 16 }}>
            <div
              style={
                this.props.isDoingTask ? { opacity: '.5' } : { opacity: '1' }
              }
            >
              <Row style={{ position: 'relative', textAlign: 'right' }}>
                <Col lg={1} md={1} sm={1} xs={3}>
                  {this.props.message.avatar
                    ? <div>
                        <Avatar
                          size={48}
                          className="avatar"
                          src={
                            process.env.REACT_APP_DOMAIN +
                            process.env.REACT_APP_IMAGES_DIRECTORY +
                            this.props.message.avatar
                          }
                        />
                      </div>
                    : <Avatar size={48} className="avatar">
                        {this.props.message.sender
                          ? this.props.message.sender.substring(0, 1)
                          : 'ن'}
                      </Avatar>}
                </Col>

                <Col lg={9} md={9} sm={9} xs={5}>
                  <div style={{ marginTop: 32, textAlign: 'right' }}>
                    <div>
                      {this.props.message.sender}
                    </div>
                  </div>
                </Col>

                <Col
                  lg={1}
                  md={1}
                  sm={1}
                  xs={2}
                  style={{ position: 'absolute', left: 32, top: 20 }}
                >
                  <IconButton
                    className="hint--bottom"
                    aria-label={DashboardStrings.attachment}
                  >
                    <a
                      href={`
                                                ${process.env
                                                  .REACT_APP_DOMAIN}${process
                        .env.REACT_APP_FILES_DIRECTORY}${this.props.message
                        .attachment}
                                            `}
                      target="_blank"
                    >
                      <Attachment />
                    </a>
                  </IconButton>
                </Col>

                <Col
                  lg={1}
                  md={1}
                  sm={1}
                  xs={2}
                  style={{ position: 'absolute', left: '-8px', top: 18 }}
                >
                  <IconButton
                    className="hint--bottom"
                    aria-label={DashboardStrings.delete}
                    onTouchTap={() =>
                      this.props.onDelete(this.props.message.id)}
                  >
                    <Delete />
                  </IconButton>
                </Col>
              </Row>

              <Row
                style={{
                  position: 'relative',
                  textAlign: 'right',
                  paddingTop: 32
                }}
              >
                <Col lg={11} md={11} sm={11} xs={10}>
                  <h3
                    style={{ fontSize: '1.15rem', margin: 0, direction: 'rtl' }}
                  >
                    {this.props.message ? this.props.message.title : ''}
                  </h3>
                  <p style={{ margin: 0, fontSize: '.8rem', direction: 'rtl' }}>
                    {this.props.message
                      ? getFormattedDateFromTimestamp(this.props.message.date)
                      : ''}
                  </p>
                </Col>
              </Row>

              <div
                style={{ textAlign: 'right', direction: 'rtl', paddingTop: 32 }}
                dangerouslySetInnerHTML={{
                  __html: this.props.message ? this.props.message.body : ''
                }}
              />
            </div>

            {this.props.isDoingTask
              ? <CircularProgress
                  size={40}
                  style={{
                    position: 'absolute',
                    left: 0,
                    top: 0,
                    right: 0,
                    bottom: 0,
                    margin: 'auto'
                  }}
                />
              : void 0}
          </Card>
        </Col>
      </Row>
    );
  }
}

export default MessageUI;



